<template>
    <div>
        <Layout>
            <PageHeader :title="title" :items="items" />
            <div class="row" v-if="$store.getters['wallet/validWallet']">
                <div class="col-md-6 col-xl-3">
                    <div class="card">
                        <div class="card-body">
                            <div class="float-end mt-2">
                                <img src="https://i.ibb.co/ngx3bH1/icon.png" height="80" />
                            </div>
                            <div>
                                <h4 class="mb-1 mt-1">
                                    $
                                    <span data-plugin="counterup">
                                        <countTo :startVal="0" :endVal="balance * price" :duration="2000" :decimals="2" v-if="balance || balance != 0"></countTo>
                                        <span v-if="balance == null">Loading..</span>
                                        <span v-if="balance == 0">0.00</span>
                                    </span>
                                </h4>
                                <p class="text-muted mb-0">Your Balance</p>
                            </div>
                            <p class="text-muted mt-3 mb-0" >
                                <span class="text-success me-1" v-if="balance != null && balance != 0">
                                    {{ balance.toFixed(2) }}
                                </span>
                                <span class="text-success me-1" v-else>
                                    0.00
                                </span>
                                FatCake Tokens
                            </p>
                        </div>
                    </div>
                </div>
                <div class="col-md-6 col-xl-3">
                    <div class="card">
                        <div class="card-body">
                            <div class="float-end mt-2">
                                <img src="https://raw.githubusercontent.com/ApeSwapFinance/apeswap-token-lists/main/assets/CAKE.svg" height="80" alt="">
                            </div>
                            <div>
                                <h4 class="mb-1 mt-1">
                                    <span data-plugin="counterup">
                                        <countTo :startVal="0" :endVal="rewards" :duration="2000" :decimals="5"></countTo>
                                    </span>
                                    CAKE
                                </h4>
                                <p class="text-muted mb-0">Total Rewards</p>
                            </div>
                            <p class="text-success mt-3 mb-0">
                                <i class="mdi mdi-currency-usd"></i>
                                {{ (rewards * cakePrice).toFixed(5) }}
                            </p>
                        </div>
                    </div>
                </div>
                <div class="col-md-6 col-xl-3">
                    <div class="card">
                        <div class="card-body">
                            <div class="float-end mt-2">
                                <img src="https://raw.githubusercontent.com/ApeSwapFinance/apeswap-token-lists/main/assets/CAKE.svg" height="80" alt="">
                            </div>
                            <div>
                                <h4 class="mb-1 mt-1">
                                    <span data-plugin="counterup">
                                        <countTo :startVal="0" :endVal="nextRewards" :duration="2000" :decimals="5"></countTo>
                                    </span>
                                    CAKE
                                </h4>
                                <p class="text-muted mb-0">Next Payout Rewards</p>
                            </div>
                            <p class="text-success mt-3 mb-0">
                                <i class="mdi mdi-currency-usd"></i>
                                {{ (nextRewards * cakePrice).toFixed(5) }}
                            </p>
                        </div>
                    </div>
                </div>
                <div class="col-md-6 col-xl-3">
                    <div class="card">
                        <div class="card-body">
                            <div class="float-end mt-2">
                                <img src="https://raw.githubusercontent.com/ApeSwapFinance/apeswap-token-lists/main/assets/CAKE.svg" height="80" alt="">
                            </div>
                            <div>
                                <h4 class="mb-1 mt-1">
                                    $
                                    <span data-plugin="counterup">
                                        <countTo :startVal="0" :endVal="cakePrice" :duration="2000" :decimals="2"></countTo>
                                    </span>
                                </h4>
                                <p class="text-muted mb-0">Cake Price</p>
                            </div>
                            <p class="text-muted mt-3 mb-0" v-if="cakeChange">
                                <span class="text-success me-1" v-if="cakeChange >= 0">
                                    <i class="mdi mdi-arrow-up-bold me-1"></i>
                                    {{ cakeChange.toFixed(2) }} %
                                </span>

                                <span class="text-danger me-1" v-if="cakeChange < 0">
                                    <i class="mdi mdi-arrow-down-bold me-1"></i>
                                    {{ cakeChange.toFixed(2) }} %
                                </span>
                                since last 24h
                            </p>
                        </div>
                    </div>
                </div>
                <!-- end col-->
                <!-- end col-->
            </div>
            <div class="row" v-else>
                <div class="col-12 col-md-12 col-lg-8 col-xl-5">
                    <div class="card">
                        <div class="card-body">
                            <h4 class="card-title">FatCake Rewards Tracking</h4>
                            <p class="card-title-desc">
                            Easily rewards checking
                            FatCake Tracking App
                            FatCake's chart, price movements, market cap, volume and more for all. Track Frictionless Rewards of FatCake and it's value in USD.
                            </p>

                            <div class="">
                                <b-alert variant="danger" show>Not connected to any wallet!</b-alert
                                >
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
            <!-- end Row -->
        </Layout>
    </div>
</template>

